import IonIcon from "@reacticons/ionicons";
import Button from "../../../../components/Button";
import { useEffect, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../../hooks/useRedux";
import { authUpdateInfo } from "../../../../redux/reducers/auth";
import Loading from "../../../../components/shared/Loading";
import { doc, getDoc } from "firebase/firestore";
import { db } from "../../../../configs/firebase.config";
import history from "../../../../redux/store/history";

export default function ConfirmInfo() {

    const [account, setAccount] = useState<any>(null)
    const uid = useAppSelector(state => state.auth.uid)

    const dispatch = useAppDispatch()
    const loading = useAppSelector(state => state.auth.isLoading)

    const [isFullName, setIsFullName] = useState<string>('')
    const [isCMND, setIsCMND] = useState<string>('')
    const [isGenre, setIsGenre] = useState<string>('')
    const [isDateOfBirth, setIsDateOfBirth] = useState<string>('')
    const [isJob, setIsJob] = useState<string>('')
    const [isIncome, setIsIncome] = useState<string>('')
    const [isAddress, setIsAddress] = useState<string>('')
    const [isPurpose, setIsPurpose] = useState<string>('')
    const [isPhoneNumber, setIsPhoneNumber] = useState<string>('')
    const [isRelationship, setIsRelationship] = useState<string>('')

    const handleUpdateInfo = () => {
        if (account?.address && account?.cmnd && account?.dateOfBirth && account?.fullName && account?.genre && account?.income && account?.job && account?.phoneNumber && account?.purpose && account?.relationship) {
            history.push('/user/payment-confirm')
        } else {
            const data = {
                fullName: isFullName,
                cmnd: isCMND,
                genre: isGenre,
                dateOfBirth: isDateOfBirth,
                job: isJob,
                income: isIncome,
                address: isAddress,
                purpose: isPurpose,
                phoneNumber: isPhoneNumber,
                relationship: isRelationship,
            }

            dispatch(authUpdateInfo(data))
        }
    }

    const getAccount = async () => {
        const docRef = doc(db, "users", uid);
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
            setAccount(docSnap.data());
        }
    }

    useEffect(() => {
        getAccount()
    }, [])


    return (
        <div className="p-4 flex flex-col items-start gap-4" style={{
          paddingBottom: 96
        }}>
            <div className="w-full"> <h1 className="font-bold text-center">Thông tin cá nhân </h1></div>
            <div className="w-full flex flex-col gap-4 max-w-[20rem] items-center">
                <div className="flex justify-center items-center gap-2 p-2 border w-full  border-[#cfcfcf]">
                    <input type="text" placeholder="Họ và tên" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsFullName(e.target.value)}
                        value={account?.fullName ? account?.fullName : isFullName}
                        disabled={account?.fullName ? true : false}
                    />
                    <IonIcon name="person" className="" />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="text" placeholder="Số CMND/CCCD" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsCMND(e.target.value)}
                        value={account?.cmnd ? account?.cmnd : isCMND}
                        disabled={account?.cmnd ? true : false}
                    />
                    <IonIcon name="card" className="" />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <select name="genre" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsGenre(e.target.value)}
                        value={account?.genre ? account?.genre : isGenre}
                        disabled={account?.genre ? true : false}
                    >
                        <option value="" disabled>Giới tính</option>
                        <option value="nam">Nam</option>
                        <option value="nu">Nữ</option>
                    </select>
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="date" placeholder="Ngày sinh" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsDateOfBirth(e.target.value)}
                        value={account?.dateOfBirth ? account?.dateOfBirth : isDateOfBirth}
                        disabled={account?.dateOfBirth ? true : false}
                    />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="text" placeholder="Nghề nghiệp" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsJob(e.target.value)}
                        value={account?.job ? account?.job : isJob}
                        disabled={account?.job ? true : false}
                    />
                    <IonIcon name="briefcase" className="" />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <select name="Income" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsIncome(e.target.value)}
                        value={account?.income ? account?.income : isIncome}
                        disabled={account?.income ? true : false}
                    >
                        <option value="" disabled>Thu nhập</option>
                        <option value="1">Dưới 5 triệu</option>
                        <option value="2">Từ 5 triệu đến 10 triệu</option>
                        <option value="3">Từ 10 triệu đến 20 triệu</option>
                        <option value="4">Trên 20 triệu</option>
                    </select>
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="text" placeholder="Địa chỉ" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsAddress(e.target.value)}
                        value={account?.address ? account?.address : isAddress}
                        disabled={account?.address ? true : false}
                    />
                    <IonIcon name="location" className="" />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="text" placeholder="Mục đích khoản vay" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsPurpose(e.target.value)}
                        value={account?.purpose ? account?.purpose : isPurpose}
                        disabled={account?.purpose ? true : false}
                    />
                    <IonIcon name="cash" className="" />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="text" placeholder="SĐT người thân" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsPhoneNumber(e.target.value)}
                        value={account?.phoneNumber ? account?.phoneNumber : isPhoneNumber}
                        disabled={account?.phoneNumber ? true : false}
                    />
                    <IonIcon name="call" className="" />
                </div>
                <div className="flex justify-center items-center gap-2 p-2 border w-full border-[#cfcfcf]">
                    <input type="text" placeholder="Mối quan hệ với người thân" className="outline-none flex-1 bg-transparent"
                        onChange={(e) => setIsRelationship(e.target.value)}
                        value={account?.relationship ? account?.relationship : isRelationship}
                        disabled={account?.relationship ? true : false}
                    />
                    <IonIcon name="people" className="" />
                </div>
                <Button kind="tertiary" className="w-auto font-bold py-2 px-5 rounded-[10rem] duration-150 hover:bg-white hover:text-primary border border-transparent hover:border-primary bg-primary"
                    onClick={() => handleUpdateInfo()}
                >
                    Tiếp tục
                </Button>
            </div>
            {
                loading && <Loading />
            }
        </div>
    )
}
